import {Component} from "react";
import * as React from "react";
import Pathfinder, {FINISHPOS, STARTPOS} from "./Pathfinder";


export default class Cell extends Component<{x: number, y: number},{}>{
    constructor(props: any) {
        super(props);
        this.state = {};
    }

    render(): any {
        let {x, y} = this.props;
        let extraClassName: string = x == STARTPOS.x && y == STARTPOS.y ? "cell-start" : x == FINISHPOS.x && y == FINISHPOS.y ? "cell-finish" : "";
        return (
            <div className={`cell ${extraClassName}`}/>
        )
    }
}